body {
  background-color: #11413f;
  font-family: "Courier New", Courier, monospace;
  text-align: center;
  margin: 0;
  padding: 0;
}

/* Heading */
h1 {
  font-size: 4rem;
  margin-top: 1.7rem;
  color: white;
}

/* Input field */
input[type="text"] {
  font-size: 1.6rem;
  padding: 10px;
  border: 2px solid hsla(0, 0%, 80%, 0.5);
  border-radius: 5px;
  color: rgb(40, 19, 19);
}

/* Buttons */
button {
  font-size: 1.7rem;
  font-weight: bold;
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.5s ease;
}

.add-btn {
  background-color: rgb(17, 148, 148);
}

.remove-btn {
  background-color: rgb(176, 112, 237);
}

.up-btn {
  background-color: rgb(246, 170, 69);
}

.down-btn {
  background-color: rgb(246, 173, 19);
}

/* Buttons inside list items */
.remove-btn,
.up-btn,
.down-btn {
  padding: 8px 12px;
  font-size: 1.4rem;
  margin-left: 10px;
}

/* Ordered list */
ol {
  padding: 0;
  list-style: none;
}

/* List items */
li {
  font-size: 2rem;
  font-weight: bold;
  padding: 15px;
  background-color: whitesmoke;
  margin-bottom: 10px;
  border: 3px solid rgb(102, 101, 101);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Text span inside list item */
.text {
  flex: 1;
  text-align: left;
}
